import React, { useState, useEffect } from 'react';
import './WorkPage.scss';
import Warp from 'warpjs'; 
import {useSpring, animated} from 'react-spring'; 
import ImagesLoaded from 'react-images-loaded';
import {JSONLD, Generic} from 'react-structured-data'

import ButtonDecorate from '../ButtonDecorate/ButtonDecorate';
import WorkPageGallery from '../WorkPageGallery/WorkPageGallery';
import MassonryGallery from '../MassonryGallery/MassonryGallery';
import {getToken, getData} from '../../store/actions'; 

const shuffle = (arr) => {
	var j, temp;
	for(var i = arr.length - 1; i > 0; i--){
		j = Math.floor(Math.random()*(i + 1));
		temp = arr[j];
		arr[j] = arr[i];
		arr[i] = temp;
	}
	return arr;
}

const WorkPage = ({data, works, allImages, currentWorkData, setCurrentWorkData, area}) => {
  const [showDetails, setShowDetails] = useState(true);
  const [contentHeight, setContentHeight] = useState(0)
  const [screenWidth, setScreenWidth] = useState(window.innerWidth); 
  const [mainPhoto, setMainPhoto] = useState(false); 

  let animIdMedia, svgMedia, warpMedia, animateMedia;
  let animateSpeed = 4; 
  let offsetMedia = 0;

  useEffect(() => {
    getToken('http://yova.praid.com.ua/api/login')
      .then(data => data.data['api_token'])
      .then(token => {
        getData(`http://yova.praid.com.ua/api/${area}/${data.id}`, token)
          .then(data => setCurrentWorkData(data))
          .catch(err => console.log(err))
      })
  }, [])

  useEffect(() => {
    setContentHeight(document.getElementById('contentShow').scrollHeight); 
    setShowDetails(false)
  }, [])

  useEffect(() => {
    window.addEventListener('resize', resize);
    window.addEventListener('orientationchange', resize);

    if (!currentWorkData && screenWidth < 850) {
      return
    }
    svgMedia = document.getElementById('buttonMedia');
    warpMedia = new Warp(svgMedia);
    warpMedia.interpolate(10);
    warpMedia.transform(([ x, y ]) => [ x, y, y ]);
    animateMedia = () => {
      warpMedia.transform(([ x, y, oy ]) => [ x, oy + animateSpeed * Math.sin(x / 16 + offsetMedia), oy ])
      animIdMedia = requestAnimationFrame(animateMedia);
      offsetMedia += 0.08;
    } 
    animateMedia();

    return () => {
      window.removeEventListener('resize', resize); 
      window.removeEventListener('orientationchange', resize);
    }
  })

  const resize = () => {
    setScreenWidth(window.innerWidth);
  }

  const currentAllImages = allImages
    .filter(item => (area === 'works' ? item.worksID : item.socialityID) === data.id)
    .sort((first, second) => first.order - second.order); 

  const showContentAnimation = useSpring({ height: showDetails ? 'auto' : 0, opacity: showDetails ? 1 : 0, visibility: showDetails ? 'visible' : 'hidden' })
  const showMainPhoto = useSpring({ opacity: mainPhoto ? 1 : 0 })
 
  const worksForAlsoLike = (arr) => {
    let newWorks = arr.filter(item => item.id != data.id)
    return shuffle(newWorks)
  }

  return (  
    <section className="work" itemscope itemtype="http://schema.org/CreativeWork" itemprop="isFamilyFriendly">
      <div className="wrapper">
        {
          currentWorkData 
          ? 
            <ImagesLoaded
              elementType={'div'}
              className={'work__image'}
              done={() => setMainPhoto(true)}
            >
              <animated.img src={currentWorkData.mainImage} alt={currentWorkData.title || "Altruist"} style={showMainPhoto} />
            </ImagesLoaded> 
          : 
            <div className="work__image"></div>
        }
        
        <h1 className="work__left-mobtitle" itemprop="name">
          {data.title}
        </h1>
          <animated.div id="contentShow" className="work__info" style={window.innerWidth < 799 ? showContentAnimation : null}>
            <div className="work__left">
              <table className="work__left-table">
                <tbody>
                  <tr>
                    <td>Location</td>
                    <td itemprop="contentLocation">{data.location}</td>
                  </tr>
                  <tr>
                    <td>Function</td>
                    <td itemprop="itemOffered">{data.function}</td>
                  </tr>
                  <tr>
                    <td>Area</td>
                    <td itemprop='materialExtent'>{data.area}</td>
                  </tr>
                  <tr>
                    <td>Status</td>
                    <td itemprop="creativeWorkStatus">{data.status}</td>
                  </tr>
                  <tr>
                    <td>Designer</td>
                    <td itemprop="author">{data.designer}</td>
                  </tr>
                  <tr>
                    <td>Art pattern</td>
                    <td itemprop="genre">{data.art_pattern}</td>
                  </tr>
                  <tr>
                    <td>Studio</td>
                    <td itemprop="locationCreated">{data.studio}</td>
                  </tr>
                  <tr>
                    <td>Photographer</td>
                    <td itemprop="contributor">{data.photographer}</td>
                  </tr>
                </tbody>
              </table>
              <div className="work__left-button">
                <a href={`http://yova.praid.com.ua/${area}pdf/${data.id}`}>
                  <ButtonDecorate 
                    title="media kit" 
                    id={'buttonMedia'} 
                    // startAnimate={startAnimate}
                    // stopAnimate={stopAnimate} 
                    autoStart={true}
                  />
                </a>
              </div>
            </div>
            <div className="work__right">
              <h1 itemprop="name" className="work__right-title">
                {data.title}
              </h1>
              {data.description.split('\r\n\r\n').map((item, index) => {
                if (index >= 3) {
                  return 
                } 
                return <p itemprop="about" key={item} className="work__right-text">
                  {item}
                </p>
                }
              )}
            </div>
          </animated.div>
          <button className="work__details" onClick={() => setShowDetails(!showDetails)}>MORE DETAILS {`${showDetails === true ? '-' : '+'}`}</button>
          <WorkPageGallery images={currentAllImages} text={data.description.split('\r\n\r\n')} />
        <h3 className='work__also'>YOU MIGHT ALSO LIKE</h3>
        <MassonryGallery 
          title={false} 
          button={false} 
          worksArr={worksForAlsoLike(works)} 
          count={4} 
          area='works' 
          photoLoadButton={true}
        />
      </div>
      <JSONLD>
        <Generic type="CreativeWork" jsonldtype="CreativeWork" 
          schema={{ 
            contentLocation: `${data.location}`,
            materialExtent: `${data.area}`, 
            creativeWorkStatus: `${data.status}`, 
            genre: `${data.art_pattern}`, 
            locationCreated: `${data.studio}`, 
            additionalType: `${data.function}`,
          }}>
          <Generic type="author" jsonldtype="Person" schema={{name: `${data.designer}`}}/>
          <Generic type="contributor" jsonldtype="Person" schema={{ name: `${data.photographer}` }}/>
        </Generic>
      </JSONLD>
      {/* <Helmet>
        <meta name="description" content="Helmet application" />
        <script  type="application/ld+json">
          {`{
            "@context":"https://schema.org/","@type":"CreativeWork","contentLocation":"Antonovicha st., 1, Kiev, Ukraine","materialExtent":"12 m2","creativeWorkStatus":"Realization 2018","genre":"Verdi","locationCreated":"YOVA YAGER hospitality design","additionalType":"Mobile van","author":{"@type":"Person","name":"Yova Yager"},"contributor":{"@type":"Person","name":"Sergei Polushko"}
          }`}
        </script>
      </Helmet> */}
    </section>
  );
}
 
export default WorkPage;